<script setup lang="ts">
import { useRoute } from "vue-router";
import SmallHeader from "@/components/headers/SmallHeader.vue";
import BackgroundImage from "@/components/background/BackgroundImage.vue";
import BackgroundGradient from "@/components/background/BackgroundGradient.vue";

const route = useRoute();

function getHeaderComponent() {
  const headerType = route.meta.header;
  switch (headerType) {
    case "small":
      return SmallHeader;
    default:
      return SmallHeader;
  }
}

function getBackgroundComponent() {
  const backgroundType = route.meta.background;
  switch (backgroundType) {
    case "image":
      return BackgroundImage;
    case "gradient":
      return BackgroundGradient;
    default:
      return BackgroundGradient;
  }
}
</script>

<template>
  <div>
    <component :is="getBackgroundComponent()"> </component>
    <component :is="getHeaderComponent()">
      <router-view />
    </component>
  </div>
</template>

<style scoped></style>
