<script lang="ts">
import { defineComponent } from "vue";

export default defineComponent({
  methods: {
    switchLanguage(locale: string) {
      this.$i18n.locale = locale;
    },
  },
});
</script>

<template>
  <div>
    <button @click="switchLanguage('en')">
      <img
        src="https://raw.githubusercontent.com/gosquared/flags/master/flags/flags/flat/32/United-Kingdom.png"
        alt="English"
      />
    </button>
    <button @click="switchLanguage('fr')">
      <img
        src="https://raw.githubusercontent.com/gosquared/flags/master/flags/flags/flat/32/France.png"
        alt="Français"
      />
    </button>
  </div>
</template>

