<script setup lang="ts">
import { fetchIllustrationsHome } from "@/illustrationService";
import { IllustratedStoryType } from "@/types/Illustration";
import { ref, onMounted } from "vue";
import IllustrationsHome from "@/components/IllustrationsHome.vue";

const loading = ref(true);

let illustrations = [] as IllustratedStoryType[];

onMounted(async () => {
  try {
    console.log("Fetching illustrations...");
    illustrations = await fetchIllustrationsHome();
    console.log("Illustrations", illustrations[0].imageUrl);
  } catch (error) {
    console.error("Error fetching illustrations", error);
  } finally {
    loading.value = false;
  }
});
</script>
<template>
  <div class="home">
    <h1>Name Lastname</h1>
    <div v-if="loading">Loading illustrations...</div>
    <div v-else>
      <illustrations-home :illustrations="illustrations" />
    </div>
  </div>
</template>

