export async function fetchIllustrationsId(
  id: number
): Promise<IllustratedStoryType> {
  if (id < 0 || id >= illustrationsHome.length) {
    return Promise.reject("Illustration not found");
  }
  return Promise.resolve(illustrationsHome[id]);
}

export async function fetchIllustrationsHome(): Promise<
  IllustratedStoryType[]
> {
  return Promise.resolve(illustrationsHome);
}

export async function fetchIllustrations(): Promise<IllustratedStoryType[]> {
  return Promise.resolve(illustrationsGroup1);
}
