<script setup lang="ts">
import { defineProps } from "vue";
import { IllustratedStoryType } from "@/types/Illustration";
import router from "@/router";

interface Props {
  illustration: IllustratedStoryType;
  side: string;
}
defineProps<Props>();

const exploreIllu = (illustration: IllustratedStoryType) => {
  if (illustration.type === "group") {
    router.push(`/group/${illustration.id}`);
  } else if (illustration.type === "illustration") {
    router.push(`/illustration/${illustration.id}`);
  }
};
</script>

<template>
  <div
    v-if="side === 'right'"
    @click="exploreIllu(illustration)"
    class="group group-right"
  >
    <div class="group-text">
      <h3 class="">{{ illustration.title }}</h3>
      <p>{{ illustration.description }}</p>
    </div>
    <img
      class="illustration"
      :src="require(`@/assets/${illustration.imageUrl}`)"
      :alt="illustration.title"
    />
  </div>
  <div v-else @click="exploreIllu(illustration)" class="group group-left">
    <img
      class="illustration"
      :src="require(`@/assets/${illustration.imageUrl}`)"
      :alt="illustration.title"
    />
    <div class="group-text">
      <h3 class="">{{ illustration.title }}</h3>
      <p>{{ illustration.description }}</p>
    </div>
  </div>
</template>

